<template>
	<div class="self" :style="height">
		<div class="self-summary">
				<div class="self-settings">
					<img src="./images/icon_setting.png" alt="">
				</div>
				<div class="self-icon">
					<img src="./images/self-logo.png" alt="">
					<h6>专属卡号：1020 88888</h6>
					<h6>会籍：至尊会员</h6>
				</div>
				<div class="self-tab">
					<div class="tab-item">
						<h5><img src="./images/icon_M.png" alt="">0.00</h5>
						<p>良元</p>
					</div>
					<div class="tab-item">
						<h5>0</h5>
						<p>我的收藏</p>
					</div>
					<div class="tab-item">
						<h5><img src="./images/icon_crown.png" alt=""></h5>
						<p>解锁特权</p>
					</div>
				</div>
		</div>
		<div class="self-form">
			<van-cell-group>
				<van-cell title="我的订单" icon="order" is-link></van-cell>
				<van-cell value="余额：0.00" icon="assets" is-link>
					<template slot="title">
						<span class="van-cell-text">我的资产</span>
					</template>
				</van-cell>
				<van-cell value="快速审批,10W额度" icon="finance" is-link>
					<template slot="title">
						<span class="van-cell-text">孕娘支票</span>
					</template>
				</van-cell>
				<van-cell value="最高额度30万" icon="money" is-link>
					<template slot="title">
						<span class="van-cell-text">现金贷款</span>
					</template>
				</van-cell>
				<van-cell value="免费送3万理财金" icon="gold" is-link>
					<template slot="title">
						<span class="van-cell-text">专属理财</span>
					</template>
				</van-cell>
				<van-cell value="最高赔付100万" icon="insurance" is-link>
					<template slot="title">
						<span class="van-cell-text">保险服务</span>
					</template>
				</van-cell>
			</van-cell-group>
		</div>
		<footers :active='2'></footers>
	</div>
</template>

<script>
import footers from '../../components/zxhwfooter';

export default {
	name: 'SelfCenter',
	data () {
		return {
		msg: 'Welcome to Your Vue.js App',
		height: ''
		}
	},
	components: {
		footers
	},
	mounted() {
		this.height = 'height:' + document.documentElement.scrollHeight + 'px'
	}
}
</script>

<style lang="less">
.self{
	width: 100%;
	background: url(./images/bg2px.jpg) repeat-x;
	background-size: auto 100%;
	.self-summary{
		color: #fff;
		padding: 48px 43px 0;
		.self-settings{
			width: 100%;
			height: 79px;
			text-align: right;
			img{
				width: 40px;
				height: 38px;
			}
		}
		.self-icon{
			padding-top: 15px;
			width: 100%;
			height: 502px;
			text-align: center;
			font-size: 28px;
			background: url(./images/iconCircle.png) top center no-repeat;
			background-size: auto 249px;
			box-sizing: border-box;
			img{
				margin-bottom: 25px;
				width: 220px;
			}
			h6{
				margin-top: 26px;
				font-weight: normal;
			}
		}
		.self-tab{
			display: flex;
			width: 100%;
			box-sizing: border-box;
			.tab-item{
				flex: 1;
				text-align: center;
				h5{
					font-size: 30px;
					img{
						margin-top: -.2rem;
						width: 32px;
						vertical-align: middle;
					}
				}
				p{
					font-size: 24px;
					line-height: 2.5;
				}
			}
		}
	}
	.self-form{
		margin-top: 50px;
		padding-bottom: 138px;
		.van-cell-group{
			color: #fff;
			background: none;
			.van-cell{
				background: none;
				color: #fff;
				border-bottom-width: 0;
				font-size: 32px;
				&::after{
					border: none;
				}
				.van-cell__value{
					font-size: 24px;
				}
				.van-icon{
					margin-right: .5rem;
					display: inline-block;
					color: #fff!important;
				}
				.van-icon-order{
					margin-left: .1rem;
					margin-right: .55rem;
					width: 32px;
					height: 38px;
					background: url(./images/icon_order.png) center no-repeat;
					background-size: 100%;
				}
				.van-icon-assets{
					width: 36px;
					height: 34px;
					background: url(./images/icon_assets.png) center no-repeat;
					background-size: 100%;
				}
				.van-icon-finance{
					width: 40px;
					height: 40px;
					background: url(./images/icon_finance.png) center no-repeat;
					background-size: 100%;
				}
				.van-icon-money{
					width: 40px;
					height: 40px;
					background: url(./images/icon_money.png) center no-repeat;
					background-size: 100%;
				}
				.van-icon-gold{
					width: 38px;
					height: 38px;
					background: url(./images/icon_gold.png) center no-repeat;
					background-size: 100%;
				}
				.van-icon-insurance{
					width: 38px;
					height: 38px;
					background: url(./images/icon_ insurance.png) center no-repeat;
					background-size: 100%;
				}
				.van-cell__right-icon{
					margin-right: 0;
				}
			}
		}
		.van-hairline--top-bottom::after{
			border: none;
		}
	}
}
</style>



